.inventory {
    border: solid;
}

.inventory-contents {
    display: grid;
    margin: 1rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-gap: 1rem;
}

.item-card{
    text-align :center;
    border: solid;
    grid-gap: 8px 8px;
}

.item-id{
    display: none;
}

.item-name{
    margin-top: 0.5rem;
    font-size: 2rem;
}

.item-price{
    margin-bottom: 0.5rem;
}